.toggle-button {
  position: fixed;
  top: 0;
  left: 0;
  background-color: transparent;
  padding: 0;
  margin-top: 10px;
  margin-left: 10px;
}
.toggle-button:hover {
  background-color: transparent;
}

.iconBurger {
  color: rgba(249, 212, 125, 1);
  font-size: 28px;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
  .toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #222222;
    height: 45px;
  }
}
@media (min-width: 769px) {
  .toolbar__dTB {
    display: none;
  }
}

.navigation {
  height: 8vh;
  width: 100vw;

  position: fixed;
  display: flex;
  justify-content: flex-end;
  /* align-items: center; */
  padding: 1vw 1%;
  background-color: rgba(44, 44, 44);

  list-style: none;
}

.li {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 2vh;

  text-decoration: none;
  padding: 10px;
  height: 20%;
  z-index: 300;
}

.toolbar li a {
  text-decoration: none;
  color: #edf0f1;
}

.toolbar li a:hover {
  color: rgba(249, 212, 125, 1);
}

.side-drawer {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  font-family: "Montserrat";
  position: fixed;
  top: 0;
  left: 0;
  list-style: none;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: 20%;
  margin-left: 10%;
}
.side-drawer li {
  margin: 0.5rem;
}
.side-drawer a {
  color: gray;
  text-decoration: none;
  font-size: 2.2rem;
}

.side-drawer a:hover {
  color: rgba(249, 212, 125, 1);
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 100;
}

ul {
  list-style: none;
  text-decoration: none;
}

