.img_inside {
  padding: 20px;
  width: 500px;
  height: 250px;
  object-fit: cover;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  cursor: pointer;
}
.img_inside:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
@media (max-width: 1024px) {
